.chart-block {
  background: #ffffff;
  border-radius: 12px;
  padding: 24px;
  box-shadow:
    0 2px 8px rgba(0, 0, 0, 0.08),
    0 8px 24px rgba(0, 0, 0, 0.12);
  transition: all 0.3s ease;
  border: 1px solid #e5e7eb;
  border-left: 4px solid #3b82f6;

  &:hover {
    box-shadow:
      0 4px 16px rgba(0, 0, 0, 0.15),
      0 12px 32px rgba(0, 0, 0, 0.1);
    border-color: #d1d5db;
  }
}

.chart-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  padding-bottom: 16px;
  border-bottom: 1px solid var(--surface-100);
}

.chart-title {
  color: var(--text-color);
  font-size: 1.125rem;
  font-weight: 700;
  margin: 0;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.chart-actions {
  display: flex;
  gap: 8px;
}

.action-btn {
  background: var(--surface-100);
  border: 1px solid var(--surface-200);
  border-radius: 6px;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.2s ease;
  color: var(--text-color-secondary);

  &:hover {
    background: var(--primary-50);
    border-color: var(--primary-200);
    color: var(--primary-600);
    transform: scale(1.1);
  }
}

.download-icon {
  font-size: 14px;
  font-style: normal;
}

.chart-container {
  position: relative;
  height: 350px;
  width: 100%;
  margin-bottom: 16px;
}

.chart-loading {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: var(--surface-0);
  border-radius: 8px;
  z-index: 10;

  p {
    margin: 12px 0 0 0;
    color: var(--text-color-secondary);
    font-size: 0.875rem;
  }
}

.loading-spinner {
  width: 32px;
  height: 32px;
  border: 3px solid var(--surface-200);
  border-top: 3px solid var(--primary-500);
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.chart-description {
  margin-top: 16px;

  p {
    color: var(--text-color-secondary);
    line-height: 1.5;
    margin: 0;
    font-size: 0.875rem;
  }
}

.chart-legend {
  margin-top: 20px;
  padding-top: 16px;
  border-top: 1px solid var(--surface-200);
}

.legend-items {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 12px;
}

.legend-item {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 12px;
  background: var(--surface-50);
  border-radius: 6px;
  transition: all 0.2s ease;

  &:hover {
    background: var(--surface-100);
    transform: scale(1.02);
  }
}

.legend-color {
  width: 12px;
  height: 12px;
  border-radius: 3px;
  flex-shrink: 0;
}

.legend-label {
  font-size: 0.875rem;
  color: var(--text-color-secondary);
  flex: 1;
}

.legend-value {
  font-size: 0.875rem;
  font-weight: 600;
  color: var(--text-color);
}

// Theme Variants
.chart-block.theme-primary {
  border-left-color: #3b82f6;
}

.chart-block.theme-success {
  border-left-color: #22c55e;
}

.chart-block.theme-warning {
  border-left-color: #f59e0b;
}

.chart-block.theme-danger {
  border-left-color: #ef4444;
}

// Dark Mode Support
:host-context(.dark) {
  .chart-block {
    background: var(--surface-800);
  }

  .action-btn {
    background: var(--surface-700);
    border-color: var(--surface-600);

    &:hover {
      background: var(--surface-600);
      border-color: var(--surface-500);
    }
  }

  .legend-item {
    background: var(--surface-700);

    &:hover {
      background: var(--surface-600);
    }
  }

  .chart-legend {
    border-top-color: var(--surface-600);
  }

  .chart-loading {
    background: var(--surface-800);
  }
}

// Responsive Design
@media (max-width: 768px) {
  .chart-block {
    padding: 16px;
  }

  .chart-container {
    height: 280px;
  }

  .chart-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 12px;
  }

  .legend-items {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 480px) {
  .chart-container {
    height: 240px;
  }
}
